import BigNumber from "bignumber.js";

export default class HbarUnit {
    /**
     * @internal
     * @param {string} name
     * @param {string} symbol
     * @param {BigNumber} tinybar
     */
    constructor(name, symbol, tinybar) {
        /**
         * @internal
         * @readonly
         */
        this._name = name;

        /**
         * @internal
         * @readonly
         */
        this._symbol = symbol;

        /**
         * @internal
         * @readonly
         */
        this._tinybar = tinybar;

        Object.freeze(this);
    }
}

HbarUnit.Tinybar = new HbarUnit("tinybar", "t", new BigNumber(1));

HbarUnit.Microbar = new HbarUnit("microbar", "", new BigNumber(100));

HbarUnit.Millibar = new HbarUnit("millibar", "m", new BigNumber(100000));

HbarUnit.Hbar = new HbarUnit("hbar", "", new BigNumber("100000000"));

HbarUnit.Kilobar = new HbarUnit(
    "kilobar",
    "k",
    new BigNumber(1000).multipliedBy(new BigNumber("100000000"))
);

HbarUnit.Megabar = new HbarUnit(
    "megabar",
    "M",
    new BigNumber(1000000).multipliedBy(new BigNumber("100000000"))
);

HbarUnit.Gigabar = new HbarUnit(
    "gigabar",
    "G",
    new BigNumber("1000000000").multipliedBy(new BigNumber("100000000"))
);
